import { useContext } from "react";

import {
  Highlights,
  ReportContainer,
  Paragraf,
  List,
  ListItem,
  Italic,
} from "./StyledComponents";

import {
  AIR_INTELIGENCE_ACTION,
  DAMAGE_ACTION,
  ADJUSTMENT_ACTION,
} from "../../assets/consts";

import { Context } from "../../store/Context";

const Report = () => {
  const {
    state: {
      data,
      timeStart,
      timeFinish,
      area,
      unit,
      action,
      aircraft,
      directionUkr,
      directionPidarasy,
      pilots,
      altitude,
      jamming,
      targets,
      subUnit,
    },
  } = useContext(Context);

  const results = {
    [AIR_INTELIGENCE_ACTION]: (
      <Paragraf>  :</Paragraf>
    ),
    [DAMAGE_ACTION]: <Paragraf>  :</Paragraf>,
    [ADJUSTMENT_ACTION]: <Paragraf>  :</Paragraf>,
  };

  const noChanging = {
    [AIR_INTELIGENCE_ACTION]: (
      <Paragraf>    .</Paragraf>
    ),
    [DAMAGE_ACTION]: <Paragraf>  .</Paragraf>,
    [ADJUSTMENT_ACTION]: <Paragraf>  .</Paragraf>,
  };

  const isJamming = jamming.selected
    ? "    ."
    : "     .";

  const pilotsList = pilots.selected
    ?.map((pilot) => `${pilot.value}`)
    .join(", ");

  const targetsList = (
    <List>
      {targets.selected.map((target) => {
        if (target.label === AIR_INTELIGENCE_ACTION) {
          return (
            <ListItem key={target.id}>
              <Paragraf>
                <Highlights>
                  {target.targetStatusAir.value}&nbsp;
                  {target.targetName.value}
                </Highlights>
                ,  &nbsp;
                {target.targetCoordinates},{" "}
                {target.targetCity.value
                  ? ` .. ${target.targetCity.value} (),`
                  : null}
                {target.targetDistance.value}   ;
              </Paragraf>
            </ListItem>
          );
        }

        return (
          <ListItem key={target.id}>
            <Paragraf>
              <Highlights>
                {target.targetStatusDamage.value}&nbsp;
                {target.targetName.value}
              </Highlights>
              ,  &nbsp;
              {target.targetCoordinates},{" "}
              {target.targetCity.value
                ? ` ..
                ${target.targetCity.value} (),`
                : null}{" "}
              &nbsp;
              {target.targetDistance.value}   . &nbsp;
              {target.countAmunition.value}&nbsp;
              {target.amunition.value}{" "}
              <Italic>({target.amunitionAction.value})</Italic>.
            </Paragraf>
          </ListItem>
        );
      })}
    </List>
  );

  const actionOptions = {
    [AIR_INTELIGENCE_ACTION]: " ",
    [DAMAGE_ACTION]: " ",
    [ADJUSTMENT_ACTION]: "  ",
  };

  return (
    <ReportContainer>
      <Highlights>{subUnit.selected?.value} 105 </Highlights>
      <Paragraf>
        {data.selected}, ({timeStart.selected?.value} -{" "}
        {timeFinish.selected?.value})
        <Highlights>  {unit.selected?.value} </Highlights>
        {actionOptions[action.selected?.value]},  {" "}
        {aircraft.selected?.value}  . ..{" "}
        {directionPidarasy.selected?.value} ()  ..{" "}
        {directionUkr.selected?.value} (.),  {area.selected?.value}.
      </Paragraf>
      {targets.selected.length
        ? results[action.selected?.value]
        : noChanging[action.selected?.value]}
      {targetsList}
      {action.selected?.value !== AIR_INTELIGENCE_ACTION && (
        <>
          <Paragraf>
            <Highlights> :</Highlights>
          </Paragraf>
          <Paragraf>.</Paragraf>
        </>
      )}
      <Paragraf>
        {isJamming}{" "}
        {action.selected?.value === AIR_INTELIGENCE_ACTION &&
          ` ${altitude.selected?.value} .`}
      </Paragraf>{" "}
      <Paragraf>: {pilotsList}.</Paragraf>
    </ReportContainer>
  );
};

export default Report;
